interface Stat {
    label: string;
    value: string;
  }
  
  interface StatsGridProps {
    stats: Stat[];
  }
  
  export default function StatsGrid({ stats }: StatsGridProps) {
    return (
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-6">
        {stats.map((stat, index) => (
          <div key={index} className="text-center p-6 bg-slate-800/30 rounded-xl border border-slate-700 hover:border-slate-600 transition-all duration-300 hover:bg-slate-800/40 hover:scale-105">
            <div className="text-xs font-mono text-slate-400 mb-2 tracking-wider">
              {stat.label}
            </div>
            <div className="text-4xl lg:text-5xl font-bold bg-gradient-to-r from-white to-slate-300 bg-clip-text text-transparent">
              {stat.value}
            </div>
          </div>
        ))}
      </div>
    );
  }